                <p>10.6. Caso qualquer disposio destas Clusulas seja tida como ilegal, invlida ou inexequvel total ou parcialmente, por qualquer legislao, essa disposio ou parte dela ser, naquela medida, considerada como no existente para os efeitos destas Clusulas, mas a legalidade, validade e exequibilidade das demais disposies contidas nestas Clusulas no sero afetadas. Nesse caso, as partes substituiro a disposio ilegal, invlida ou inexequvel, ou parte dela, por outra que seja legal, vlida e exequvel e que, na mxima medida possvel, tenha efeito similar  disposio tida como ilegal, invlida ou inexequvel para fins de contedo e finalidade das presentes Clusulas do Cdigo de Conduta. Estas Clusulas constituem a totalidade do acordo e entendimento das partes sobre este assunto e substituem e prevalecem sobre todos os entendimentos e compromissos anteriores sobre este assunto. Nestas Clusulas, as palavras inclusive e inclui significam incluindo, sem limitao.</p>
